import MainNavBar from '../components/layout/MainNavBar';
import { Redirect, useParams } from 'react-router-dom';
import './ChatPage2.css'
import orImage from '../media/images/or.png'
import { Button, Col, Form, FormControl, InputGroup, Row } from 'react-bootstrap';
import axios from 'axios';
import { FormEvent, useEffect, useState } from 'react';
import React from 'react';
import { SocketContext } from '../socket/context';
import Utils from "../components/utils/utils";
import { useUser } from '../components/context/UserAuthContext';

interface IStatus {
	[username: string]: string
}

interface IMessage {
	id: number,
	author: string,
	content: string
}

interface IChannel {
	admin: string,
	name: string,
	password: string
}

function ChatPage(props: any) {

	const [Friends, setFriends] = useState([]);
	const [ChanList, setChanList] = useState<IChannel[]>([]);
	const [isLoading, setLoading] = useState(true);
	const [Status, setStatus] = useState<IStatus>({});
	const [Channel, setChannel] = useState("");
	const [ChanToggle, setChanToggle] = useState(false);
	const [ChannelName, setChannelName] = useState("");
	const [ChanPwd, setChanPwd] = useState("");
	const [CurrentChan, setCurrentChan] = useState("");
	const [Message, setMessage] = useState('');
	const [Value, setValue] = useState(0);
	const [MsgList, setMsgList] = useState<IMessage[]>([]);
	const { user, setUser } = useUser()!;
	const socket = React.useContext(SocketContext);
	const param: any = useParams();

	var channel: string = param.id.substring(1);
	var chatDiv = document.getElementById("chat");
	if (chatDiv)
		chatDiv.scrollTop = chatDiv.scrollHeight;

	function ListItem(friends: any) {
		return (
			<li className="row" key={friends.username}>
				<div className="col-3 avatar">
					<img src={friends.avatar} width='30px' height='30px' alt=""></img>
				</div>
				<div className="col userinfo">
					<a href={'#profile/:' + friends.username}>{friends.username}</a>
					<p><span className={Status[friends.username] == 'online' ? "status green"
					: Status[friends.username] == 'offline' ? 'status orange' : 'status blue'}>
					</span>{Status[friends.username]}</p>
				</div>
		</li>
		)
	}

	function sendMessage(event: FormEvent) {
		event.preventDefault();
		var value = Value + 1;
		console.log('send');
		socket.emit('send_message', {
			'channel': channel,
			'content': Message
		})
		setValue(value);
		setMessage("");
	}

	function setChan() {
		setCurrentChan(channel);
	}

	function ListChan(chans: any) {
		return (
			<a key={chans.name} href={'#chat/:' + chans.name} 
			onClick={setChan}>
			<li className="row">
				<div className="col chaninfo">
					{chans.name}
				</div>
			</li></a>
		)
	}

	function ListMsg(message: any) {
		return (
			<li key={message.id} className={message.author == user.username ? 'me' : 'you'}>
				<div className="entete">
					<h2>{message.author}</h2>
					{/* <h3>10:12AM, Today</h3> */}
				</div>
			<div className="message">
			{message.content}
		</div>
		</li>
	)
	}

	function toggleChannel() {
		setChanToggle(!ChanToggle);
	}

	function createChannel(event:FormEvent) {
		event.preventDefault();
		var chan: IChannel = {admin: user.username, name: ChannelName, password: ChanPwd};
		console.log('call');
		if (ChanList.find(element => element.name == chan.name)) {
			alert('This channel already exists');
		}
		else {
			setChanList([...ChanList, chan]);
			socket.emit('create_channel', chan);
			console.log(ChanList);
		}
	}

	useEffect(() => {
		console.log('loop');
		Utils.intercept401(props);
		if (user.id > 0)
			socket.emit('login', user.username);
		axios.get(`${process.env.REACT_APP_BASE_URL}/authentication/friends`,
		{ withCredentials: true})
		.then((response) => {
			if (response.data) {
			setFriends(response.data);
			var stat: IStatus = {};
			for (var i = 0 ; i < response.data.length ; i++) {
				stat[response.data[i].username] = response.data[i].status
			}
			setStatus(stat);
		}
		})
		axios.get(`${process.env.REACT_APP_BASE_URL}/chat/channels`,
		{ withCredentials: true})
		.then((response) => {
			if (response.data) {
				var list: IChannel[] = [];
				for (var i = 0 ; i < response.data.length ; i++) {
					list[i] = {admin: response.data[i].admin, 
						name: response.data[i].name, 
						password: response.data[i].password};
				}
				setChanList(list);
			}
		})
		axios.post(`${process.env.REACT_APP_BASE_URL}/chat/messages`,
		{channel: channel},
		{ withCredentials: true}).then((response) => {
			if (response.data) {
				console.log('okok');
				setMsgList(response.data);
			}
			setLoading(false);
		})
	}, [channel])

	useEffect(() => {
		socket.on('status', (data: any) => {
			var stat = Status;
			stat[data.username] = data.status;
			setStatus(stat);
		})
	}, [Status])

	useEffect(() => {
		console.log('ok');
		socket.on('channel_created', (data: any) => {
			var value = Value + 1;
			var newchan: IChannel = {admin: data.admin, name: data.name, password: data.password}
			setChanList([...ChanList, newchan]);
			setValue(value);
		})
	}, [Value])

	useEffect(() => {
		console.log('receive');
		socket.on('receive_message', (data: any) => {
			var msg: IMessage = {id: data.id,
								author: data.author,
								content: data.content};
			setMsgList([...MsgList, msg]);
			var chatDiv = document.getElementById("chat");
			if (chatDiv)
				chatDiv.scrollTop = chatDiv.scrollHeight;
		})
	}, [Value])

	if (user.id > 0 && isLoading)
		return (<div><MainNavBar/></div>)
	else if (user.id > 0)
		return (
		<div>
				<MainNavBar/>
				<div className="container-fluid">
				<div className="row">
				<div className="col-2 row-height">
					<div className="btnb mt-2">
						<button className="btn btn-secondary" onClick={toggleChannel}>
							Create a channel
						</button>
					</div>
					{ChanToggle ? 
					<div className="chantoggle">
						<Form onSubmit={createChannel}>
							<Form.Control size="sm" type="text" placeholder="Channel name"
								autoFocus
								value={ChannelName}
								onChange={(e) => setChannelName(e.target.value)} />
							<Form.Control size="sm" type="text" placeholder="Password (optional)" 
								value={ChanPwd}
								onChange={(e) => setChanPwd(e.target.value)}/>
							<Button type="submit" className="btn btn-secondary">Create</Button>
						</Form>
					</div> : ""}
					<hr/>
					<h5 id='subTitle'>FRIENDS</h5>
					<div className="friendlist">
						<ul className="ul">
							{Friends.map((listvalue) => {
								return (ListItem(listvalue))
							})}
						</ul>
					</div>
					<hr/>
					<h5 id='subTitle'>CHANNELS</h5>
					<div className="friendlist">
						<ul className="chanlist">
							{ChanList.map((listvalue) => {
								return (ListChan(listvalue))
							})}
						</ul>
						</div>
				</div>
				
				<div className="col-10 row-chat">
					<div className="row chatheader">
						<p>{channel}</p>
					</div>
					<div className="row chatbox">
						<ul id="chat">
							{MsgList ? MsgList.map((listvalue) => {
								return (ListMsg(listvalue))
							}) : ""}
						</ul>
					</div>
					<div className="row chatfooter">
						<Form onSubmit={sendMessage}>
							<Row className="align-items-center">
								<Col sm={10} className="my-1">
									<Form.Control
													autoFocus
													as="textarea" 
													rows={5} 
													style={{ width: '100%', resize: "none" }} 
													placeholder="Message" 
													value={Message}
													onChange={(e) => setMessage(e.target.value)}/>
								</Col>
								<Col xs="auto" className="my-1">
									<Button type="submit">Submit</Button>
								</Col>
							</Row>
						</Form>
					</div>
				</div>
				</div>
				</div>
		</div>
	);
	else
        return (<Redirect to={{ pathname: "/login", state: { from: props.location} }} />);
}

export default ChatPage
